.home h1 {
  padding: 10px;
  text-align: center;
}
.welcomeBox {
  display: flex;
  flex-direction: column;
  gap: 1em;
  margin: 0 auto;
  padding: 10px;
  max-width: 900px;
}

.fadeIn {
  animation: fadeIn 2s;
  -webkit-animation: fadeIn 2s;
  -moz-animation: fadeIn 2s;
  -o-animation: fadeIn 2s;
  -ms-animation: fadeIn 2s;
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.aboutBox,
.skillsBox {
  margin: 1em auto;
  padding: 10px;
  max-width: 1200px;
  display: grid;
  gap: 1em;
  grid-template-columns: 1fr;
}

.skillsBox {
  background-color: #9cafb7;
}
.skillsContent {
  grid-row: 1/2;
}

.aboutBox img {
  justify-self: center;
  align-self: center;
  width: 70%;
}

.homeWrapper,
.skilsWrapper {
  background-color: var(--secondaryColor, #f0ebce);
  padding: 5px;
}

@media screen and (min-width: 768px) {
  .welcomeBox {
    gap: 1em;
    margin-top: 5em;
    margin-bottom: 6em;
  }
}

.text {
  margin-bottom: 1em;
}

@media screen and (min-width: 1000px) {
  .welcomeBox {
    margin-top: 11em;
    margin-bottom: 11em;
  }
}
